import React, { Component } from 'react';
import { Card, CardBody, Row, Col } from 'reactstrap';
import { Doughnut } from 'react-chartjs-2';
import moment from 'moment';
import 'moment/locale/id';
import TotalQueue from './TotalQueue';
import Flatpickr from 'react-flatpickr'
import '../../node_modules/flatpickr/dist/themes/airbnb.css';
import 'flatpickr/dist/l10n/id'

export default class MainDash extends Component {
  state = {
    queues_count: 0,
    arrived: 0,
    unarrived: 0,
    unserved: 0,
    date : [new Date(), new Date()]
  }

  componentDidMount = () => {
    document.title = "Halaman Utama";
    moment.locale('id');
    this.getQueuesCount();
    this.getArrivedQueues();
    this.getUnarrivedQueues();
    this.getUnservedQueues();
  }

  getQueuesCount = () => {
    const { models: { Queue } } = this.props;
    Queue.collection({ limit: 0, offset: 0, where: { date: new Date() } }).then(q => this.setState({ queues_count: q.count }))
  }

  getArrivedQueues = () => {
    const { models: { Queue } } = this.props;
    const {date} = this.state;
    Queue.collection({
      limit: 0, offset: 0, where: {
        date: date.length > 0 ? {
          $between : [moment(date[0]).format('YYYY-MM-DD hh:mm:ss'), moment(date[1]).format('YYYY-MM-DD hh:mm:ss')]
        } : moment(date[0]).format('YYYY-MM-DD'),
        status: 'Datang'
      }
    }).then(q => this.setState({ arrived: q.count }))
  }

  getUnarrivedQueues = () => {
    const { models: { Queue } } = this.props;
    const {date} = this.state;
    Queue.collection({
      limit: 0, offset: 0, where: {
        date: date.length > 0 ? {
          $between : [moment(date[0]).format('YYYY-MM-DD hh:mm:ss'), moment(date[1]).format('YYYY-MM-DD hh:mm:ss')]
        } : moment(date[0]).format('YYYY-MM-DD'),
        status: 'Tidak Datang'
      }
    }).then(q => this.setState({ unarrived: q.count }))
  }

  getUnservedQueues = () => {
    const { models: { Queue } } = this.props;
    const {date} = this.state;
    Queue.collection({
      limit: 0, offset: 0, where: {
        date: date.length > 0 ? {
          $between : [moment(date[0]).format('YYYY-MM-DD hh:mm:ss'), moment(date[1]).format('YYYY-MM-DD hh:mm:ss')]
        } : moment(date[0]).format('YYYY-MM-DD'),
        status: 'Belum Datang'
      }
    }).then(q => this.setState({ unserved: q.count }))
  }

  render() {
    return (
      <div className="card-body">
        <h5 className="text-right m-0">Main Dashboard</h5>
        <hr />
        <Row noGutters>
          <Col md="2">
            <Flatpickr value={this.state.date} options={{
              locale : 'id',
              mode : 'range'
            }} onChange={date => this.setState({ date }, () => {
              this.total.getPurposes();
              this.getArrivedQueues();
              this.getQueuesCount();
              this.getUnarrivedQueues();
              this.getUnservedQueues();
            })} className="form-control form-control-sm" placeholder="Pilih tanggal" />
          </Col>
          <Col md="12" />
          <Col md="3">
            <Card className="shadow border-0 bg-info text-white mx-1 my-2">
              <CardBody className="d-flex flex-row justify-content-between align-items-center">
                <div className="d-flex flex-column justify-content-between">
                  <h4 className="m-0">Pendaftar</h4>
                  <p className="m-0 small">{moment(this.state.date[0]).format('DD MMMM YYYY')}</p>
                </div>
                <h1 style={{ fontSize: '3.5em' }} className="m-0">{this.state.queues_count}</h1>
              </CardBody>
            </Card>
          </Col>
          <Col md="3">
            <Card className="shadow border-0 bg-primary text-white mx-1 my-2">
              <CardBody className="d-flex flex-row justify-content-between align-items-center">
                <div className="d-flex flex-column justify-content-between">
                  <h4 className="m-0">Datang</h4>
                  <p className="m-0 small">{moment(this.state.date[0]).format('DD MMMM YYYY')}</p>
                </div>
                <h1 style={{ fontSize: '3.5em' }} className="m-0">{this.state.arrived}</h1>
              </CardBody>
            </Card>
          </Col>
          <Col md="3">
            <Card className="shadow border-0 bg-warning text-dark mx-1 my-2">
              <CardBody className="d-flex flex-row justify-content-between align-items-center">
                <div className="d-flex flex-column justify-content-between">
                  <h4 className="m-0">Tidak Datang</h4>
                  <p className="m-0 small">{moment(this.state.date[0]).format('DD MMMM YYYY')}</p>
                </div>
                <h1 style={{ fontSize: '3.5em' }} className="m-0">{this.state.unarrived}</h1>
              </CardBody>
            </Card>
          </Col>
          <Col md="3">
            <Card className="shadow border-0 bg-dark text-white mx-1 my-2">
              <CardBody className="d-flex flex-row justify-content-between align-items-center">
                <div className="d-flex flex-column justify-content-between">
                  <h4 className="m-0">Belum Terlayani</h4>
                  <p className="m-0 small">{moment(this.state.date[0]).format('DD MMMM YYYY')}</p>
                </div>
                <h1 style={{ fontSize: '3.5em' }} className="m-0">{this.state.unserved}</h1>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <hr/>
        <Row>
          <Col md="6">
            <Doughnut data={{
              datasets: [{
                data: Object.keys(this.state).filter(k => k !== 'queues_count').map(k => this.state[k]),
                backgroundColor: [
                  'rgba(54, 162, 235, 0.5)',
                  'rgba(255, 99, 132, 0.5)',
                  'rgba(52, 58, 64, 0.5)',                  
                ],
                borderColor: [
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 99, 132, 1)',
                  'rgba(52, 58, 64, 1)',
                ],
              }],
              labels: ['Datang', 'Tidak Datang', 'Belum Dilayani']
            }} width={120} height={75} />
          </Col>
          <Col md="6">
            <TotalQueue ref={ref => this.total = ref} date={this.state.date} models={this.props.models} />
          </Col>
        </Row>
      </div>
    )
  }
}
