import React, { Component } from 'react';
import { Table, Button, Card, CardBody, ButtonGroup, Spinner, UncontrolledTooltip } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import { IoIosDocument, IoIosClipboard, IoIosRedo, IoMdClose, IoMdCheckmark } from 'react-icons/io'
import moment from 'moment';
import _ from 'lodash';
import 'moment/locale/id';
import PreviewDocuments from './PreviewDocuments';

export default class Queue extends Component {
  state = {
    queues: {
      count: 1,
      rows: []
    },
    user: {
      name: '',
      id: '',
      type: ''
    },
    loading: false,
    id: 0,
    limit: 25,
    offset: 0,
    totalPage: 1,
    page: 1
  }

  getQueues = (refresh = true) => {
    const { models: { Queue } } = this.props;
    let { totalPage, limit, offset } = this.state;
    if (refresh) this.setState({ loading: true })
    Queue.collection({
      limit, offset, include:
        [{ model: 'Purpose', attributes: ['name', 'id'] }],
      // { model: 'Document', attributes: ['name', 'id', 'data'] }],
      order: [['time', 'asc'], ['id', 'asc']],
      where: {
        date: moment().format('YYYY-MM-DD'),
        status: 'Belum Datang'
      }
    }).then(queue => {
      const queues = _.groupBy(queue.rows, 'time');
      totalPage = Math.ceil(queue.count / limit);
      this.setState({ queues: { count: queue.count, rows: queues }, totalPage, loading: false })
    });
  }

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getQueues());
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getQueues());
  }

  componentDidMount = () => {
    this.getQueues(true)
    moment.locale('id');
    const { socket, provider } = this.props;
    document.title = "Antrian Hari Ini"
    provider.get().then(user => this.setState({ user }, () => {
      document.title = `${this.state.user.name} | Antrian Hari Ini`
    }));
    socket.on('UPDATE_LIST', () => this.getQueues(false));
    socket.on('NEW_QUEUE', () => this.getQueues(false));
    socket.on('QUEUE_CALLED', () => this.getQueues(false));
    socket.on('QUEUE_ARRIVED', () => this.getQueues(false));
  }

  updateQueue = (q, called) => {
    // console.log(called)
    q.update({ called }).then(() => this.getQueues(false));
  }

  updateStatus = (q, status) => {
    q.update({ status }).then(() => this.getQueues());
  }

  preview = id => this.setState({ id }, () => this.previewDocs.open());

  render() {
    if (this.state.type === 'administrator') return (<Redirect to="/dashboard/main" />)
    return (
      <>
        <div className="card-body">
          <h5 className="m-0">Antrian</h5>
          <hr />
          {this.state.loading &&
            <Card className="bg-dark border-0 shadow text-white">
              <CardBody><h4 className="text-center m-0"><Spinner color="light" />&nbsp;Loading</h4></CardBody>
            </Card>
          }
          {Object.keys(this.state.queues.rows).length === 0 && !this.state.loading && <Card className="text-white bg-dark border-0 shadow-sm">
            <CardBody><h5 className="text-center m-0">Tidak ada antrian hari ini</h5></CardBody>
          </Card>}
          {!this.state.loading && Object.keys(this.state.queues.rows).sort().map((time, i) => (
            <Card className="my-2 bg-dark text-white border-0 shadow" key={i}>
              <CardBody><h5 className="m-0">{time}</h5></CardBody>
              <Table size="sm" responsive={true} dark className="text-white" hover striped>
                <thead>
                  <tr>
                    <th className="pl-3">No. antrian</th>
                    <th>Nama</th>
                    <th>Tujuan</th>
                    <th>Tanggal - Jam</th>
                    <th>NIK</th>
                    <th>No. Telp</th>
                    <th>Aksi</th>
                  </tr>
                </thead>
                <tbody>
                  <React.Fragment key={i}>
                    {this.state.queues.rows[time].map((que, index) => (<Tr
                      updateStatus={this.updateStatus}
                      data={que} user={this.state.user}
                      onClick={this.updateQueue}
                      q={time}
                      rows={this.state.queues.rows}
                      previewDocuments={this.preview}
                      index={index}
                      key={index} />
                    ))}
                  </React.Fragment>
                </tbody>
              </Table>
            </Card>
          ))}
          <hr />
          {this.state.totalPage > 0 && <ButtonGroup size="sm">
            <Button color="primary" disabled={this.state.page === 1} onClick={this.prev}>Prev</Button>
            <Button color="light" disabled>{this.state.page}/{this.state.totalPage}</Button>
            <Button color="primary" disabled={this.state.totalPage === 1} onClick={this.next}>Next</Button>
          </ButtonGroup>}
        </div>
        <PreviewDocuments ref={ref => this.previewDocs = ref} models={this.props.models} id={this.state.id} />
      </>
    )
  }
}

const Tr = props => (
  <tr>
    <>
      <td className="pl-3">{props.data.queue_number}</td>
      <td>{props.data.name.toUpperCase()}</td>
      <td>{props.data.purpose.name}</td>
      <td>{moment(props.data.date).format('DD MMMM YYYY')} - {props.data.time}</td>
      <td>{props.data.nik}</td>
      <td>{props.data.phone}</td>
      <td>
        <ButtonGroup size="sm">
          <Button onClick={() => props.previewDocuments(props.data.id)} id={`document${props.data.id}`} size="sm" color="light"><IoIosDocument /></Button>
          <UncontrolledTooltip placement="left-start" target={`document${props.data.id}`}>Dokumen {props.data.name}</UncontrolledTooltip>
          {props.data.called === 0 &&
            <>
              <Button id={`call${props.data.id}`} onClick={() => props.onClick(props.data, props.user.id)} color="primary" size="sm"><IoIosClipboard /></Button>
              <UncontrolledTooltip placement="left" target={`call${props.data.id}`}>Panggil {props.data.name}</UncontrolledTooltip>
            </>
          }
          {props.data.called !== 0 && props.data.called !== props.user.id &&
            <>
              <Button id={`called${props.data.id}`} size="sm" color="secondary"><IoIosClipboard /></Button>
              <UncontrolledTooltip placement="left" target={`called${props.data.id}`}>Sudah dipanggil</UncontrolledTooltip>
            </>
          }
        </ButtonGroup>&nbsp;&nbsp;
        {props.data.called !== 0 && props.data.called === props.user.id &&
          <>
            <Button size="sm" id={`uncheck${props.data.id}`} className="rounded-pill" color="danger" onClick={() => props.updateStatus(props.data, 'Tidak Datang')}><IoMdClose /></Button>&nbsp;&nbsp;
            <UncontrolledTooltip placement="left" target={`uncheck${props.data.id}`}>Tidak Datang</UncontrolledTooltip>
            <Button size="sm" id={`check${props.data.id}`} className="rounded-pill" onClick={() => props.updateStatus(props.data, 'Datang')} color="warning" size="sm"><IoMdCheckmark /></Button>&nbsp;&nbsp;
            <UncontrolledTooltip placement="left" target={`check${props.data.id}`}>Datang</UncontrolledTooltip>
            <Button size="sm" id={`reset${props.data.id}`} className="rounded-pill" onClick={() => props.onClick(props.data, 0)} color="dark" size="sm"><IoIosRedo /></Button>
            <UncontrolledTooltip placement="left" target={`reset${props.data.id}`}>Reset</UncontrolledTooltip>
          </>
        }
      </td>
    </>
  </tr>
)