
import React, { Component } from 'react';
import { Button, Modal, ModalBody, ModalHeader, ButtonGroup, Col, Spinner, Alert } from 'reactstrap';
import AddSetting from './AddSetting';
import SettingCard from './SettingCard';
import AddDate from './AddDate';
import { IoMdAdd } from 'react-icons/io';
import EditQuota from './EditQuota';

export default class Settings extends Component {
  state = {
    schedules: {
      rows: [],
      count: 1
    },
    modal: false,
    schedule: {
      date: new Date(),
      limitations: []
    },
    addSchedule: false,
    totalPage: 1,
    page: 1,
    offset: 0,
    limit: 4,
    loading: false,
    editQuota: false
  }

  getSchedule = (refresh = false) => {
    const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit } = this.state;
    if (refresh) this.setState({ loading: true })
    Schedule.collection({
      limit,
      offset,
      order: [['date', 'asc']],
      include: [{
        model: 'Limitation', attributes: ['limit', 'purpose_id', 'id'],
        include: [{ model: 'Purpose', attributes: ['name'] }]
      }],
      attributes: ['date', 'processing_time', 'tolerance', 'break_start', 'break_end', 'operator', 'open', 'close'],
      distinct: true
    }).then(schedules => {
      totalPage = Math.ceil(schedules.count / limit);
      this.setState({ schedules, totalPage, loading: false })
    });
  }

  componentDidMount = () => {
    this.getSchedule(true);
  }

  delete = schedule => {
    // this.setState({ modal : false })
    // console.log(schedule.delete);
    schedule.delete().then(() => {
      this.setState({ schedule: {}, modal: false });
      this.getSchedule();
    })
  }

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getSchedule());
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getSchedule());
  }

  first = () => {
    this.setState({ offset: 0, page: 1 }, () => this.getSchedule());
  }

  last = () => {
    const { totalPage, limit } = this.state;
    const offset = parseInt((totalPage - 1) * limit);
    this.setState({ offset, page: totalPage }, () => this.getSchedule());
  }

  manageOperator = (schedule, type = 'add') => {
    if (type === 'add') {
      const { operator, date, open, close, tolerance, processing_time, break_end, break_start } = schedule;
      schedule.update({ operator: operator + 1, date, open, close, tolerance, processing_time, break_end, break_start }).then(resp => {
        console.log(resp);
        this.getSchedule();
      }).catch(e => console.log(e))
    } else if (type === 'subtract') {
      const { operator, date, open, close, tolerance, processing_time, break_end, break_start } = schedule;
      schedule.update({ operator: operator === 1 ? 1 : operator - 1, date, open, close, tolerance, processing_time, break_end, break_start }).then(resp => {
        console.log(resp);
        this.getSchedule();
      }).catch(e => console.log(e))
    }
    // console.log(schedule.operator)
  }

  calculateTotal = ({
    tolerance,
    processing_time,
    start_time,
    end_time,
    break_start,
    break_end,
    operator
  }) => {

    const interval = parseInt(tolerance) + parseInt(processing_time);

    const st = { h: start_time.split(':')[0], m: start_time.split(':')[1], s: '00' };
    const et = { h: end_time.split(':')[0], m: end_time.split(':')[1], s: '00' };

    const bs = { h: break_start.split(':')[0], m: break_start.split(':')[1], s: '00' };
    const be = { h: break_end.split(':')[0], m: break_end.split(':')[1], s: '00' };

    const today = new Date();

    let s = new Date(today.getFullYear(), today.getMonth(), today.getDate(), st.h, st.m, st.s);
    let e = new Date(today.getFullYear(), today.getMonth(), today.getDate(), et.h, et.m, et.s);

    let bso = new Date(today.getFullYear(), today.getMonth(), today.getDate(), bs.h, bs.m, bs.s);
    let beo = new Date(today.getFullYear(), today.getMonth(), today.getDate(), be.h, be.m, be.s);

    console.log(interval)
    let total = 0;


    while (s.getTime() < e.getTime()) {
      s.setMinutes(s.getMinutes() + interval);
      if (s.getTime() < bso.getTime() || s.getTime() > beo.getTime()) {
        total++;
      }
    }

    return total * operator;
  }


  render() {
    document.title = "Booking | Pengaturan"
    const { schedules: { rows }, editQuota, schedule } = this.state;
    const { models } = this.props
    return (
      <>
        <div className="card-body">
          <h4 className="m-0">Pengaturan</h4>
          <hr />
          <Button outline onClick={() => this.addSetting.open()} color="success"><IoMdAdd />&nbsp;Tambah Pengaturan</Button>
          {/* <Button outline color="success" className="rounded-pill" onClick={() => this.setState({ addSchedule: true })} ><IoMdAdd /></Button> */}
          <hr />
          <div className="row">
            {this.state.loading ? <Col md="12">
              <Alert color="dark" className="text-center"><h4 className="m-0"><Spinner />&nbsp;Loading</h4></Alert>
            </Col>
              :
              rows.length === 0 ? <Col md="12">
                <Alert color="info" className="text-center"><h4 className="m-0">Tidak Ada Pengaturan</h4></Alert>
              </Col> : <></>
            }
            {rows.map((row, i) => (<SettingCard quota={this.calculateTotal({
              tolerance: row.tolerance,
              start_time: row.open,
              end_time: row.close,
              break_end: row.break_end,
              break_start: row.break_start,
              operator: row.operator,
              processing_time: row.processing_time
            })} manageOperator={this.manageOperator} editQuota={schedule => this.setState({ schedule, editQuota: true })} deleteSetting={schedule => this.setState({ schedule, modal: true })} key={i} setting={row} />))}
            {this.state.addSchedule && <AddDate lastDate={rows[(rows.length - 1)].date} />}
          </div>
          <div className="d-flex flex-row justify-content-between align-items-center my-3">
            {this.state.totalPage > 0 &&
              <ButtonGroup size="sm">
                <Button disabled={this.state.loading} outline onClick={this.first} color="primary">First</Button>
                <Button disabled={this.state.loading} outline onClick={this.prev} color="dark">Prev</Button>
                <Button color="light" disabled>Halaman {this.state.page}/{this.state.totalPage}</Button>
                <Button disabled={this.state.loading} outline onClick={this.next} color="dark">Next</Button>
                <Button disabled={this.state.loading} outline onClick={this.last} color="primary">Last</Button>
              </ButtonGroup>
            }
          </div>
        </div>
        <AddSetting get={this.getSchedule} ref={ref => this.addSetting = ref} models={this.props.models} />
        <DeleteSetting schedule={this.state.schedule} delete={schedule => this.delete(schedule)} isOpen={this.state.modal} close={() => this.setState({ modal: false })} />
        {Object.values(schedule).length > 2 && <EditQuota open={editQuota} schedule={schedule} models={models} close={() => this.setState({ schedule: {}, editQuota: false })} />}
      </>
    )
  }
}

const DeleteSetting = props => (
  <Modal isOpen={props.isOpen}>
    <ModalHeader className="bg-danger" toggle={props.close} />
    <ModalBody>
      <h5 className="text-center">Apakah Anda yakin ingin menghapus penganturan ini?</h5>
      <div className="row my-3">
        <div className="col-md-6">
          <Button color="danger" onClick={() => props.delete(props.schedule)} block>Ya</Button>
        </div>
        <div className="col-md-6">
          <Button color="light" onClick={props.close} block>Tidak</Button>
        </div>
      </div>
    </ModalBody>
  </Modal>
)