import React, { useEffect, useState } from 'react'
import { Modal, ModalBody, ModalHeader, InputGroup, Input, InputGroupAddon, InputGroupText, ModalFooter, Alert, Button } from 'reactstrap'

export default function EditQuota({ open, close, schedule, models: { Limitation } }) {
  const [limitations, setLimits] = useState([{ purpose: { name: '' }, limit: 0, id: 0 }]);
  const [limitSize, setSize] = useState(0);
  const [success, toggleSuccess] = useState(false);

  useEffect(() => {
    setLimits(schedule.limitations)
  }, [schedule]);

  const updateQuota = () => {
    limitations.forEach((limit, index) => {
      Limitation.single(limit.id).then(resp => {
        resp.update({ limit: limit.limit }).then((resp) => {
          console.log(resp);
          setSize(index + 1);
        }).catch(e => alert(e));
      }).catch(e => alert(e))
    })
  }

  useEffect(() => {
    if (limitSize === limitations.length) {
      toggleSuccess(true)
      setTimeout(() => {
        setSize(0);
        toggleSuccess(false)
        close();
      }, 5000);
    }
  }, [limitSize])

  return (
    <Modal size="lg" toggle={close} isOpen={open}>
      <ModalHeader toggle={close} title="Edit Limit Pengantri">Edit Limit Pengantri</ModalHeader>
      <ModalBody>
        {success && <Alert color="success" >Limit pengantri berhasil di-update</Alert>}
        {
          limitations.map((limit, i) => (
            <InputGroup key={limit.id} style={{ margin: '5px 0' }}>
              <InputGroupAddon addonType="prepend">
                <InputGroupText>{limit.purpose.name}</InputGroupText>
              </InputGroupAddon>
              <Input type="number" onChange={ev => {
                limitations[i].limit = ev.target.value;
                setLimits([...limitations])
              }} value={limit.limit} />
            </InputGroup>
          ))
        }
      </ModalBody>
      <ModalFooter>
        <Button color="primary" onClick={updateQuota} >Update</Button>
      </ModalFooter>
    </Modal>
  )
}
