import React, { Component } from 'react'
import { Card, CardBody, Badge } from 'reactstrap';
import moment from 'moment';

export default class TotalQueue extends Component {
    state = {
        purposes: [],
        purposes_total: [],
        loading : false,
        date : [new Date()]
    }
    componentDidMount = () => {
        this.getPurposes()
    }

    componentWillReceiveProps = ({ date }) => {
        this.setState({ date })
    }

    getPurposes = () => {
        this.setState({ loading  : true, purposes_total : []})
        const { models: { Purpose } } = this.props;
        Purpose.collection({
            limit: 999, offset: 0, attributes: ['id', 'name'],            
        }).then(({ rows }) => {
            this.setState({loading : false}, () => {
                rows.map(row => this.getTotalQueuePerPurpose(row))
            })
        });
    }

    getTotalQueuePerPurpose = (purpose) => {
        const { models: { Queue } } = this.props;
        Queue.collection({
            limit: 0, offset: 0, where: {
                purpose_id: purpose.id,
                date : this.state.date.length > 0 ? {
                    $between : [moment(this.state.date[0]).format('YYYY-MM-DD hh:mm:ss'), moment(this.state.date[1]).format('YYYY-MM-DD hh:mm:ss')]
                } : moment(this.state.date[0]).format('YYYY-MM-DD')
            }
        }).then(({ count }) => {
            const { purposes_total } = this.state;
            const { name } = purpose;
            purposes_total.push({ name, count })
            this.setState({ purposes_total });
        })
    }

    render() {
        return (
            <>
                {this.state.loading && <p className="text-center m-0">Loading...</p>}
                {this.state.purposes_total.map((p, i) => (<Card key={i} color="dark" className="border-0 shadow my-2">
                    <CardBody className="text-white p-2 d-flex justify-content-between align-items-center">
                        <p className="d-block m-0">{p.name.toUpperCase()}</p>
                        <Badge color="danger" className="d-block p-2" pill>{p.count}</Badge>
                    </CardBody>
                </Card>))}
            </>
        )
    }
}
