import React from 'react';
import {NavLink} from 'react-router-dom';
import {IoIosLogOut, IoIosHome, IoIosJournal, IoIosPaper, IoIosPerson, IoIosCog} from 'react-icons/io'

const DashLink = props => (<div className="list-group list-group-flush">
  <NavLink to="/dashboard/main" activeClassName="bg-danger text-white" className="list-group-item list-group-item-action"><IoIosHome />&nbsp;Main Dashboard</NavLink>
  <NavLink to="/dashboard/rekap-antrian" activeClassName="bg-danger text-white" className="list-group-item list-group-item-action"><IoIosJournal />&nbsp;Laporan</NavLink>
  <NavLink to="/dashboard/tujuan" activeClassName="bg-danger text-white" className="list-group-item list-group-item-action"><IoIosPaper />&nbsp;Dokumen &amp; Tujuan</NavLink>
  <NavLink to="/dashboard/user" activeClassName="bg-danger text-white" className="list-group-item list-group-item-action"><IoIosPerson />&nbsp;User</NavLink>
  <NavLink to="/dashboard/pengaturan" activeClassName="bg-danger text-white" className="list-group-item list-group-item-action"><IoIosCog />&nbsp;Pengaturan</NavLink>
  <div className="logout-btn">
    <button onClick={() => {
      props.provider.remove().then(() => {
        props.logout()
      })
    }} className="btn btn-outline-dark btn-block rounded-pill"><IoIosLogOut />&nbsp;Logout</button>
  </div>
</div>)

export default DashLink;