import React, { Component } from 'react';
import { Modal, ModalBody, ModalHeader, ModalFooter, Input, InputGroup, InputGroupAddon, Button, Alert, Spinner, Label, Row, Col } from 'reactstrap';
import Flatpickr from 'react-flatpickr';
import 'flatpickr/dist/themes/airbnb.css'
import { FaTimes, FaSave } from 'react-icons/fa'
import moment from 'moment';
import InputGroupText from 'reactstrap/lib/InputGroupText';

export default class AddSetting extends Component {
  state = {
    openModal: false,
    date: '',
    open: "08:00:00",
    close: "16:00:00",
    break_start: "13:00:00",
    break_end: "14:00:00",
    tolerance: 5,
    processing_time: 5,
    operator: 5,
    dates: [],
    success: false,
    loading: false,
    error: false,
    purposes: []
  }

  open = () => {
    // const { requirements: { name } } = this.props;
    this.setState({ openModal: true })
  };

  calculateTotal = ({
    tolerance,
    processing_time,
    start_time,
    end_time,
    break_start,
    break_end,
    operator
  }) => {

    const interval = parseInt(tolerance) + parseInt(processing_time);

    const st = { h: start_time.split(':')[0], m: start_time.split(':')[1], s: '00' };
    const et = { h: end_time.split(':')[0], m: end_time.split(':')[1], s: '00' };

    const bs = { h: break_start.split(':')[0], m: break_start.split(':')[1], s: '00' };
    const be = { h: break_end.split(':')[0], m: break_end.split(':')[1], s: '00' };

    const today = new Date();

    let s = new Date(today.getFullYear(), today.getMonth(), today.getDate(), st.h, st.m, st.s);
    let e = new Date(today.getFullYear(), today.getMonth(), today.getDate(), et.h, et.m, et.s);

    let bso = new Date(today.getFullYear(), today.getMonth(), today.getDate(), bs.h, bs.m, bs.s);
    let beo = new Date(today.getFullYear(), today.getMonth(), today.getDate(), be.h, be.m, be.s);

    let total = 0;


    while (s.getTime() < e.getTime()) {
      s.setMinutes(s.getMinutes() + interval);
      if (s.getTime() < bso.getTime() || s.getTime() > beo.getTime()) {
        total++;
      }
    }

    return total * operator;
  }

  onChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
  }

  close = () => this.setState({ openModal: false });


  componentDidMount() {
    this.getDates();
    // this.getPurpose();
  }

  save = ev => {
    ev.preventDefault();
    const { models: { Schedule, Limitation } } = this.props;
    const { name, date, break_end, break_start, open, close, tolerance, processing_time, operator, purposes } = this.state;
    this.setState({ loading: true });
    Schedule.create({ name, date: moment(date[0]).format('YYYY-MM-DD'), break_end, break_start, open, close, tolerance, processing_time, operator })
      .then((resp) => {
        this.setState({ success: true, loading: false, date: '' });
        this.getDates();
        this.props.get();
        purposes.forEach(async ({ limit, id: purpose_id }) => {
          try {
            const limitation = await Limitation.create({ limit, schedule_id: resp.id, purpose_id })
            console.log(limitation)
          } catch (error) {
            console.log(error)
          }
        })
      })
      .catch(er => {
        console.log(er)
        this.setState({ error: true, loading: false });
      })
  }

  getDates = () => {
    const { models: { Schedule } } = this.props;
    Schedule.collection({ limit: 9999, offset: 0 }).then(r => {
      const { rows } = r;
      const dates = rows.map(row => row.date);
      this.setState({ dates, date: dates[0] }, () => this.getPurpose());
    })
      .catch(e => alert(e));
  }

  getPurpose = () => {
    const { models: { Purpose } } = this.props;
    const { tolerance, break_end, break_start, operator, processing_time, open: start_time, close: end_time } = this.state;
    const quota = this.calculateTotal({ tolerance, break_end, break_start, start_time, end_time, operator, processing_time })
    Purpose.collection({ limit: 999, offset: 0 })
      .then(resp => {
        this.setState({ purposes: resp.rows.map(row => ({ id: row.id, name: row.name, limit: 0 })) })
      })
      .catch(e => alert(e))
  }

  closeAlert = () => this.setState({ success: false });

  render() {
    const opt = {
      minDate: moment().format('Y-MM-DD'),
      disable: this.state.dates
    }
    const { tolerance, break_end, break_start, operator, processing_time, open: start_time, close: end_time, purposes } = this.state;
    return (
      <>
        <Modal size="lg" className="setting-modal" isOpen={this.state.openModal}>
          <ModalHeader toggle={this.close} className="bg-success text-white">Tambah Jadwal</ModalHeader>
          <ModalBody>
            <Row>
              <Col md="6">
                <label htmlFor="" className="my-2">Tanggal</label>
                <Flatpickr options={opt} className="form-control" placeholder="Tanggal" onChange={date => this.setState({ date })} value={this.state.date} />
                <label htmlFor="" className="my-2">Waktu Penanganan</label>
                <Input type="number" disabled={this.state.loading} value={this.state.processing_time} name="processing_time" onChange={this.onChange} />
                <label htmlFor="" className="my-2">Jumlah Operator</label>
                <Input type="number" disabled={this.state.loading} value={this.state.operator} name="operator" onChange={this.onChange} />
                <InputGroup className="my-2">
                  <InputGroupAddon addonType="prepend">Jam Buka</InputGroupAddon>
                  <Input type="time" disabled={this.state.loading} name="open" onChange={this.onChange} placeholder="Jam Buka" value={this.state.open} id="" />
                  <InputGroupAddon addonType="prepend">Jam Tutup</InputGroupAddon>
                  <Input type="time" disabled={this.state.loading} name="close" onChange={this.onChange} placeholder="Jam Tutup" value={this.state.close} id="" />
                </InputGroup>
                <InputGroup className="my-2">
                  <InputGroupAddon addonType="prepend">Jam Istirahat</InputGroupAddon>
                  <Input type="time" disabled={this.state.loading} name="break_start" onChange={this.onChange} placeholder="Jam Istirahat" value={this.state.break_start} id="" />
                  <InputGroupAddon addonType="prepend">s/d</InputGroupAddon>
                  <Input type="time" disabled={this.state.loading} name="break_end" onChange={this.onChange} placeholder="Jam Kerja" value={this.state.break_end} id="" />
                </InputGroup>
                <label htmlFor="" className="my-2">Toleransi</label>
                <InputGroup className="my-2">
                  <Input type="number" disabled={this.state.loading} name="tolerance" onChange={this.onChange} placeholder="Jam Tutup" value={this.state.tolerance} id="" />
                  <InputGroupAddon addonType="append">Menit</InputGroupAddon>
                </InputGroup>
                <Label>Kuota Pengantri</Label>
                <InputGroup>
                  <Input readOnly value={this.calculateTotal({ tolerance, break_end, break_start, start_time, end_time, operator, processing_time })} />
                  <InputGroupAddon addonType="append">Orang</InputGroupAddon>
                </InputGroup>
                <hr />
                <Alert color="info">Waktu pengerjaan + waktu toleransi harus habis dibagi 60</Alert>
                <Alert color="success" isOpen={this.state.success} toggle={this.closeAlert}>Tanggal berhasil ditambahkan</Alert>
                <Alert color="info" isOpen={this.state.loading}><Spinner color="info" size="sm" />&nbsp;Loading</Alert>
                <Alert color="danger" isOpen={this.state.error}>Gagal menambah tanggal</Alert>
              </Col>
              <Col md="6">
                <p>Limit pengantri per tujuan</p>
                {
                  purposes.map(purpose => (
                    <InputGroup style={{ margin: '5px 0' }} key={purpose.id}>
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          {purpose.name}
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="number" value={purpose.limit} onChange={ev => {
                        const { target: { value } } = ev;
                        const limit = this.calculateTotal({ tolerance, break_end, break_start, start_time, end_time, operator, processing_time });
                        purpose.limit = (value) > limit ? parseInt(limit) : parseInt(value);
                        this.setState({ purposes })
                      }} />
                    </InputGroup>
                  ))
                }
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="success" onClick={this.save} outline><FaSave />&nbsp;Simpan</Button>
            <Button color="danger" onClick={this.close} outline><FaTimes />&nbsp;Tutup</Button>
          </ModalFooter>
        </Modal>
      </>
    )
  }
}
