import React, { Component } from 'react'
import { CardBody, Button, ButtonGroup, Table, Row, Col, Input, InputGroup, InputGroupAddon, InputGroupText, Spinner, Modal, ModalBody, UncontrolledTooltip, ModalFooter } from 'reactstrap'
import { FaSortDown, FaSortUp, FaFilter, FaCalendarTimes, FaSearch, FaEdit, FaTimes, FaCheck } from 'react-icons/fa';
import { IoIosArrowBack, IoIosArrowForward, IoIosTrash, IoIosDocument, } from 'react-icons/io';
import Flatpickr from 'react-flatpickr';
import moment from 'moment';
import 'flatpickr/dist/themes/airbnb.css';
import 'moment/locale/id';
import PreviewDocuments from './PreviewDocuments';
import ExportExcel from './ExportExcel';

export default class QueueRecap extends Component {
  state = {
    tooltips: [
      { open: false },
      { open: false },
    ],
    msg: '',
    limit: 10,
    offset: 0,
    page: 1,
    totalPage: 1,
    queues: {
      rows: [],
      count: 1
    },
    loading: false,
    date: [],
    id: 0,
    order_by: 'queue_number',
    order: 'asc',
    filter: '',
    purposes: {
      rows: [],
      count: 1
    },
    queue: {
      name: '',
      nik: '',
      queue_number: '',
      id: null
    },
    confirm: false,
    export: false,
    dataset: [{
      columns: ["No.", "NIK", "Nama", 'Nomor Antrian', 'Jam', 'Tanggal', 'Tujuan'],
      data: []
    }],
    status: '',
    query: '',
    searchBy: 'name',
    edit: false,
    status_edit: '',
  }

  componentDidMount = () => {
    document.title = "Laporan Antrian"
    moment.locale('id');
    this.getQueues(true);
    this.getPurpose();
  }

  tooltipToggle = () => {
    this.setState({ tooltips: [{ open: false }, { open: false }] })
  }

  onHover = ev => {
    const { tooltips } = this.state;
    // console.log(ev.target.name)
    if (typeof ev.target.name !== 'undefined') tooltips[parseInt(ev.target.name)].open = true;
    this.setState({ tooltips });
  }

  clearDate = () => this.setState({ date: [] }, () => this.getQueues(true))

  getQueues = (loading = false) => {
    if (loading) this.setState({ loading: true })
    const { models: { Queue } } = this.props;
    const { limit, offset, order_by, order, filter: id, date, status, searchBy, query } = this.state;
    const purpose = { model: 'Purpose', attributes: ['name', 'id'] };
    const config = {
      limit, offset, include: [purpose],
      where: {
        [searchBy]: { $like: query.length > 0 ? `%${query}%` : '%%' }
      },
      order: [[order_by, order]],
    }

    if (date.length > 0) {
      Object.assign(config.where, {
        date: date.length > 1 ? {
          $between: [moment(date[0]).format('YYYY-MM-DD hh:mm:ss'), moment(date[1]).format('YYYY-MM-DD hh:mm:ss')]
        } : moment(date[0]).format('YYYY-MM-DD')
      });
    }

    if (status.length > 0) {
      Object.assign(config.where, { status });
    }

    if (id !== '') {
      Object.assign(purpose, { where: { id } })
    }


    Queue.collection(config)
      .then(queues => {
        const totalPage = Math.ceil(queues.count / limit);
        const { dataset } = this.state;
        // ["No.", "NIK", "Nama", 'Nomor Antrian', 'Jam', 'Tanggal', 'Tujuan']
        dataset[0].data = queues.rows.map((row, i) => ([
          {
            value: `${i + 1}`, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
          {
            value: row.nik, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
          {
            value: row.name, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
          {
            value: row.queue_number, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
          {
            value: row.time, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
          {
            value: row.date, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
          {
            value: row.purpose.name, style: {
              border: {
                top: { style: 'thin', color: { rgb: '000000' } },
                bottom: { style: 'thin', color: { rgb: '000000' } },
                left: { style: 'thin', color: { rgb: '000000' } },
                right: { style: 'thin', color: { rgb: '000000' } },
              }
            }
          },
        ]))
        this.setState({ loading: false, queues, totalPage, dataset });
      })
      .catch(e => console.log(e));
  }

  getPurpose = () => {
    const { models: { Purpose } } = this.props;
    Purpose.collection({ limit: 999, offset: 0 }).then(purposes => {
      this.setState({ purposes });
    }).catch(err => alert(err));
  }

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getQueues(true));
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getQueues(true));
  }

  onLimitChange = ev => {
    if (ev.target.value >= 1)
      this.setState({ limit: ev.target.value < 1 ? 1 : ev.target.value }, () => this.getQueues(true))
  };

  onDateChange = date => this.setState({ date }, () => this.getQueues(true));

  previewDocs = id => this.setState({ id }, () => this.preview.open());

  order = ev => {
    ev.preventDefault();
    let { order } = this.state;
    if (order === 'asc') {
      order = 'desc'
    } else if (order === 'desc') {
      order = 'asc'
    }
    this.setState({ order, order_by: ev.target.name }, () => this.getQueues(true))
    return false;
  }

  cancelDelete = () => {
    this.setState({
      confirm: false,
      queue: {
        name: '',
        queue_number: '',
        nik: ''
      }
    })
  }

  updateQueueStatus = () => {
    const { queue, status_edit: status } = this.state;
    queue.update({ status }).then(() => this.setState({
      queue:
      {
        id: null,
        name: '',
        queue_number: '',
        nik: ''
      },
      edit: false,
    }, this.getQueues))
  }

  deleteQueue = queue => {
    // console.log(queue);
    queue.delete().then(() => {
      this.setState({
        confirm: false,
        queue: {
          name: '',
          queue_number: '',
          nik: ''
        }
      }, this.getQueues);
    });
  }

  export = () => this.setState({ export: true })

  render() {
    const opt = {
      mode: "range",
      dateFormat: 'Y-m-d',
    }
    const { id } = this.state
    return (
      <>
        <CardBody className="border-bottom border-primary">
          <h5 className="m-0">Laporan</h5>
          <hr />
          <Row className="justify-content-between" noGutters>
            <Col md="1">
              <ButtonGroup size="sm">
                <Button onClick={this.prev} disabled={this.state.totalPage === 0} color="primary"><IoIosArrowBack /></Button>
                <Button disabled color="dark">{`${this.state.page}/${this.state.totalPage === 0 ? 1 : this.state.totalPage}`}</Button>
                <Button onClick={this.next} disabled={this.state.totalPage === 0} color="primary"><IoIosArrowForward /></Button>
              </ButtonGroup>
            </Col>
            {!this.state.loading && <Col md="1">
              {(!this.state.loading || this.state.queues.rows.length > 0) && <ExportExcel data={this.state.dataset} />}
            </Col>}
            <Col md="3" className="pr-2">
              <InputGroup size="sm">
                <InputGroupAddon addonType="prepend"><InputGroupText><FaFilter />&nbsp;Tujuan</InputGroupText></InputGroupAddon>
                <Input onChange={ev => this.setState({ filter: ev.target.value }, this.getQueues)} value={this.state.filter} type="select">
                  <option value="">Filter per tujuan</option>
                  {this.state.purposes.rows.map(p => (<option value={p.id} key={p.id}>{p.name}</option>))}
                </Input>
              </InputGroup>
            </Col>
            <Col md="3" className="pr-2">
              <InputGroup size="sm">
                <Flatpickr value={this.state.date} options={opt} onChange={this.onDateChange} placeholder="Tanggal" className="form-control form-control-sm" />
                <InputGroupAddon addonType="append"><Button color="danger" onClick={this.clearDate}><FaCalendarTimes /></Button></InputGroupAddon>
              </InputGroup>
            </Col>
            <Col md="3">
              <InputGroup size="sm">
                <Input value={this.state.query} type="text" onChange={ev => this.setState({ query: ev.target.value }, this.getQueues)} placeholder="Cari pengantri" />
                <InputGroupAddon addonType="append">
                  <Button color="success" outline><FaSearch /></Button>
                </InputGroupAddon>
              </InputGroup>
            </Col>
          </Row>
        </CardBody>
        <ConfirmDelete open={this.state.confirm} onDelete={this.deleteQueue} data={this.state.queue} onCancel={this.cancelDelete} />
        <div className="table-responsive">
          <Table bordered hover striped>
            <thead>
              <tr>
                <th><p onClick={this.order} name="queue_number">No. Antrian</p><OrderIcon orderBy={this.state.order_by} name="queue_number" order={this.state.order} /></th>
                <th><p onClick={this.order} name="time">Jam</p><OrderIcon orderBy={this.state.order_by} name="time" order={this.state.order} /></th>
                <th><p onClick={this.order} name="date">Tanggal</p><OrderIcon orderBy={this.state.order_by} name="date" order={this.state.order} /></th>
                <th><p onClick={this.order} name="name">Nama</p><OrderIcon orderBy={this.state.order_by} name="name" order={this.state.order} /></th>
                <th><p onClick={this.order} name="phone">No. Telp</p><OrderIcon orderBy={this.state.order_by} name="phone" order={this.state.order} /></th>
                {/* <th><a href="#" onClick={this.order} name="purpose">Tujuan</a></th> */}
                <th>Tujuan</th>
                <th><p onClick={this.order} name="nik">NIK</p><OrderIcon orderBy={this.state.order_by} name="nik" order={this.state.order} /></th>
                <th><p onClick={this.order} name="status">Status</p><OrderIcon orderBy={this.state.order_by} name="status" order={this.state.order} /></th>
                <th>File/Hapus</th>
              </tr>
            </thead>
            <tbody>
              {this.state.queues.rows.length === 0 && !this.state.loading &&
                <tr><td className="text-center text-muted" colSpan={document.getElementsByTagName('th').length}>Belum ada antrian</td></tr>
              }
              {this.state.loading && <tr>
                <td className="text-center text-muted" colSpan={document.getElementsByTagName('th').length}><Spinner size="sm" color="secondary" />&nbsp;Loading</td>
              </tr>}
              {!this.state.loading && this.state.queues.rows.map(queue => <Queue
                onClick={this.previewDocs}
                onDelete={queue => this.setState({ queue, confirm: true })}
                data={queue}
                key={queue.id}
                onEdit={queue => this.setState({ edit: true, queue, status_edit: queue.status })}
                cancelEdit={() => this.setState({ edit: false, queue: { id: 0 } })}
                edit={this.state.edit}
                queue={this.state.queue}
                statusValue={this.state.status_edit}
                updateQueueStatus={this.updateQueueStatus}
                onChange={status_edit => this.setState({ status_edit })}
              />)}
            </tbody>
          </Table>
        </div>
        <CardBody className="border-top border-primary">
          <Row className="justify-content-between" noGutters>
            <Col md="2">
              <ButtonGroup size="sm">
                <Button onClick={this.prev} disabled={this.state.totalPage === 0} color="primary"><IoIosArrowBack /></Button>
                <Button disabled color="dark">{`${this.state.page}/${this.state.totalPage === 0 ? 1 : this.state.totalPage}`}</Button>
                <Button onClick={this.next} disabled={this.state.totalPage === 0} color="primary"><IoIosArrowForward /></Button>
              </ButtonGroup>
            </Col>
            {!this.state.loading && <Col>
              <code>Total : {this.state.queues.count} pendaftar</code>
            </Col>}
            {!this.state.loading && <Col md="2" className="pr-1">
              <select name="" value={this.state.status} onChange={e => this.setState({ status: e.target.value }, () => this.getQueues(true))} className="form-control form-control-sm" id="">
                <option value="">Filter by status</option>
                <option value="Datang">Datang</option>
                <option value="Tidak Datang">Tidak Datang</option>
                <option value="Belum Datang">Belum Datang</option>
              </select>
            </Col>}
            <Col md="2">
              <InputGroup size="sm">
                <Input value={this.state.limit} onChange={this.onLimitChange} type="number" />
                <InputGroupAddon addonType="append">Data/Halaman</InputGroupAddon>
              </InputGroup>
            </Col>
          </Row>
        </CardBody>
        <PreviewDocuments models={this.props.models} id={id} ref={ref => this.preview = ref} />
      </>
    )
  }
}

const OrderIcon = props => (
  props.orderBy === props.name &&
    props.order === 'asc' ? <FaSortUp /> : props.orderBy === props.name &&
      props.order === 'desc' ? <FaSortDown /> : ''
)

const Queue = props => (
  <tr>
    <td>{props.data.queue_number}</td>
    <td>{props.data.time}</td>
    <td>{moment(props.data.date).format('DD MMMM YYYY')}</td>
    <td>
      {props.edit ?
        <>
          <p id={`name${props.data.id}`} className="m-0">{props.data.name.toUpperCase().substr(0, 4) + "..."}</p>
          <UncontrolledTooltip target={`name${props.data.id}`}>Nama : {props.data.name.toUpperCase()}</UncontrolledTooltip>
        </>
        :
        <p id={`name${props.data.id}`} className="m-0">{props.data.name.toUpperCase()}</p>
      }
    </td>
    <td>
      {props.edit ?
        <>
          <p id={`phone${props.data.id}`} className="m-0">{props.data.phone.toString().substr(0, 4) + "..."}</p>
          <UncontrolledTooltip target={`phone${props.data.id}`}>No. Telp : {props.data.phone}</UncontrolledTooltip>
        </>
        :
        <p id={`phone${props.data.id}`} className="m-0">{props.data.phone}</p>
      }
    </td>
    <td>{props.data.purpose.name}</td>
    <td>
      {props.edit ?
        <>
          <p id={`nik${props.data.id}`} className="m-0">{props.data.nik.toString().substr(0, 4) + "..."}</p>
          <UncontrolledTooltip target={`nik${props.data.id}`}>NIK : {props.data.nik}</UncontrolledTooltip>
        </>
        :
        <p id={`nik${props.data.id}`} className="m-0">{props.data.nik}</p>
      }
    </td>
    <td>{
      (props.edit && props.queue.id === props.data.id) ?
        <InputGroup size="sm">
          <Input type="select" bsSize="sm" value={props.statusValue} onChange={ev => props.onChange(ev.target.value)}>
            <option value="Belum Datang">Belum Datang</option>
            <option value="Tidak Datang">Tidak Datang</option>
            <option value="Datang">Datang</option>
          </Input>
          <InputGroupAddon addonType="append"><Button onClick={props.updateQueueStatus} color="success"><FaCheck /></Button></InputGroupAddon>
        </InputGroup>
        :
        props.data.status
    }</td>
    <td>
      {(!props.edit) ?
        <>
          <UncontrolledTooltip target={`edit-btn${props.data.id}`}>Edit status {props.data.name}</UncontrolledTooltip>
          <Button color="primary" onClick={() => props.onEdit(props.data)} className="rounded-pill" id={`edit-btn${props.data.id}`} size="sm" ><FaEdit /></Button>&nbsp;&nbsp;
        </>
        :
        (props.queue.id !== props.data.id) ? <>
          <UncontrolledTooltip target={`edit-btn${props.data.id}`}>Edit status {props.data.name}</UncontrolledTooltip>
          <Button color="primary" onClick={() => props.onEdit(props.data)} className="rounded-pill" id={`edit-btn${props.data.id}`} size="sm" ><FaEdit /></Button>&nbsp;&nbsp;
        </> :
          <><Button color="danger" className="rounded-pill" onClick={props.cancelEdit} id={`cancel-btn${props.data.id}`} size="sm" ><FaTimes /></Button>&nbsp;&nbsp;</>
      }
      <ButtonGroup size="sm">
        <Button color="info" onClick={() => props.onClick(props.data.id)} size="sm"><IoIosDocument /></Button>
        <Button color="danger" onClick={() => props.onDelete(props.data)} ><IoIosTrash /></Button>
      </ButtonGroup>
    </td>
  </tr>
)

const ConfirmDelete = props => (
  <Modal isOpen={props.open}>
    <ModalBody>
      <h5 className="m-0">Apakah Anda ingin menghapus antrian</h5>
    </ModalBody>
    <Table>
      <tr>
        <td>Nama</td>
        <td>:</td>
        <td>{props.data.name}</td>
      </tr>
      <tr>
        <td>NIK</td>
        <td>:</td>
        <td>{props.data.nik}</td>
      </tr>
      <tr>
        <td>Nomor Antrian</td>
        <td>:</td>
        <td>{props.data.queue_number}</td>
      </tr>
    </Table>
    <ModalFooter>
      <Button onClick={props.onCancel} color="light" size="sm">Batal</Button>
      <Button color="danger" onClick={() => props.onDelete(props.data)} size="sm">Hapus</Button>
    </ModalFooter>
  </Modal>
)